import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { UntypedFormGroup, UntypedFormBuilder, Validators, UntypedFormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { Observable } from 'rxjs';
import { DataService } from 'src/app/shared/services/data-service';
import { startWith, map } from 'rxjs/operators';
import { Configuration } from 'src/app/app.configuration';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { ConfirmDialogComponent } from 'src/app/shared/confirm-dialog/confirm-dialog.component';
import { Http } from '@angular/http';
import * as questionaire from '../../../assets/questionaire.json';
import * as saveAs from 'file-saver';
import { HttpClient } from '@angular/common/http';
import { CustomAnalyzeComponent } from 'src/app/shared/custom-analyze/custom-analyze.component';

@Component({
  selector: 'app-questionarie-details',
  templateUrl: './questionarie-details.component.html',
  styleUrls: ['./questionarie-details.component.scss']
})
export class QuestionarieDetailsComponent implements OnInit {

  questionarieForm: UntypedFormGroup;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;

  public barChartOptions: any = {
    scaleShowVerticalLines: false,
    responsive: true,
    options: {
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true
          }
        }]
      }
    },
    animation: {
      onComplete: function () {
        var chartInstance = this.chart,
          ctx = chartInstance.ctx;
        ctx.textAlign = 'center';
        ctx.textBaseline = 'bottom';
        this.data.datasets.forEach(function (dataset, i) {
          var meta = chartInstance.controller.getDatasetMeta(i);
          meta.data.forEach(function (bar, index) {
            var data = dataset.data[index];
            ctx.fillText(data, bar._model.x, bar._model.y - 5);
          });
        });
      }
    },
  };
  public barChartType: string = 'bar';
  public barChartLegend: boolean = false;

  public barChartLabels = [];
  public barChartData: any[] = [];
  public barChartColors: any[] = [];

  selectedWorkshopId = '';
  workshopList = [];
  filteredOptions: Observable<any[]>;
  customerFilteredOptions: Observable<any[]>;
  myControl = new UntypedFormControl();
  participantList = [];
  fileDetails: {};
  questionarieId: any;
  isEdit: boolean;
  createdBy: any;
  fileName: any;
  pdfUrl = '';
  reportPdfUrl = '';
  confirmDialogRef: any;
  displayedColumns = ['position', 'analysedBy', 'analysedDate', 'categoryIncluded', 'terminologyType', 'actions']
  analyzedList = [];
  tabIndex = 0;
  analyzedListPaginated = [];
  public json: any = [];
  public chartOptions = {
    scales: {
      yAxes: [{
        ticks: {
          stepSize: 10,
          beginAtZero: true
        }
      }]
    },
    legend: { display: false }
  }
  data: any;
  analysedBy: any;
  questionarieStatus = '';

  constructor(
    private fb: UntypedFormBuilder,
    public dialog: MatDialog,
    private storageConfiguration: StorageConfiguration,
    private dataService: DataService,
    private configuration: Configuration,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private router: Router,
    private route: ActivatedRoute,
    private http: HttpClient
  ) {

    this.questionarieForm = this.fb.group({
      workshop: [''],
      workshopDate: [''],
      workshopConductedBy: [''],
      participant: [''],
      participantTitle: [''],
      organizationName: [''],
      managerTitle: [''],
      managerName: [''],
      mobile: [''],
      officeMobile: ['']
    })

  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.questionarieId = params.questionarieId;
    });
    this.getWorkshopList();

    if (this.questionarieId != '' && this.questionarieId != undefined) {
      this.isEdit = true;
      this.editQuestionarieDetails();
    }

  }

  createQuestionarie() {
    let questionarieForm = this.questionarieForm.value;
    if (this.selectedWorkshopId != '' && questionarieForm.participant != '') {
      let questionarieModel = {
        workshop: this.selectedWorkshopId,
        participant: questionarieForm.participant,
        file: this.fileDetails,
        status: 'new',
        createdBy: this.storageConfiguration.sessionGetItem(this.storageConfiguration.id),
        updatedBy: this.storageConfiguration.sessionGetItem(this.storageConfiguration.id)
      }

      this.spinner.show();
      this.dataService.addUser(this.configuration.questionaire, questionarieModel).subscribe(
        (data: any) => {
          if (data.isSuccess == true) {
            this.toastr.success('Questionaire Created Successfully!!');
            this.questionarieId = data.data._id;
            this.router.navigate(['/map/questionarie/questionarie-details', { questionarieId: data.data._id }]);
            this.isEdit = true;
            this.editQuestionarieDetails();
            this.spinner.hide();
            this.questionarieStatus = data.data.status ? data.data.status : '';
          }
        }, err => {
          this.spinner.hide();
        }
      );
    }
    else {
      this.toastr.warning('Please enter the required fields');
    }
  }

  editQuestionarieDetails() {
    this.spinner.show();
    this.dataService.getSingle(this.questionarieId, this.configuration.questionaire + '/').subscribe(
      async (data: any) => {
        let questionarieInfo = data.data;
        this.questionarieForm.patchValue({
          workshop: questionarieInfo.workshop.name,
          workshopDate: questionarieInfo.workshop.workshopDate,
          workshopConductedBy: questionarieInfo.workshop.conductedBy ?
            questionarieInfo.workshop.conductedBy.firstName + ' ' + questionarieInfo.workshop.conductedBy.lastName : '',
          participant: questionarieInfo.participant._id,
          participantTitle: questionarieInfo.participant.title,
          organizationName: questionarieInfo.participant.organizationName,
          managerTitle: questionarieInfo.participant.manager.name,
          managerName: questionarieInfo.participant.manager.title,
          mobile: questionarieInfo.participant.address.mobile,
          officeMobile: questionarieInfo.participant.address.officeMobile,
        });
        this.createdBy = questionarieInfo.createdBy ? questionarieInfo.createdBy._id : '';
        this.questionarieStatus = questionarieInfo.status ? questionarieInfo.status : '';
        this.fileDetails = questionarieInfo.file;

        this.selectedWorkshopId = this.selectedWorkshopId ? this.selectedWorkshopId : questionarieInfo.workshop._id;
        this.pdfUrl = questionarieInfo.file ? questionarieInfo.file.url : '';
        this.fileName = questionarieInfo.file ? questionarieInfo.file.fileName : '';
        this.getParticipantList();
        this.spinner.hide();

        let analyzedPdfResults = questionarieInfo.results;

        if (analyzedPdfResults != undefined && analyzedPdfResults.length > 0) {
          let graphResults = analyzedPdfResults;
          let graphData = graphResults.slice(-1)[0].analyzed_json;
          this.analysedBy = graphResults.slice(-1)[0].analyze_by.user_name;
          let url = graphData;
          let jsonOutput = [];
          this.http.get(url).subscribe((jsondata: any) => {
            jsonOutput = jsondata.overal_summary_by_category;
            this.barChartData = [];
            let barData = [];
            this.barChartLabels = [];
            this.barChartColors = [];
            // Graph
            for (const element of jsonOutput) {
              this.barChartLabels.push(element.category);
              this.barChartColors.push(element.color_code);
              barData.push(element.count);
            }
            // this.barChartData = [
            //   {
            //     data: barData,
            //     backgroundColor: this.barChartColors
            //   },
            // ];

            this.data = {
              labels: this.barChartLabels,
              datasets: [
                {
                  label: 'Count',
                  backgroundColor: this.barChartColors,
                  borderColor: this.barChartColors,
                  data: barData
                }
              ]
            }
          });
        }

        let analyzedPdfResultsLists = [];
        if (analyzedPdfResults != undefined && analyzedPdfResults.length > 0) {
          for (let i = 0; i < analyzedPdfResults.length; i++) {
            let analyzedPdfResultsItem: any = {};
            analyzedPdfResultsItem.analysedBy = analyzedPdfResults[i].analyze_by.user_name;
            analyzedPdfResultsItem.analysedById = analyzedPdfResults[i].analyze_by.user_id
            analyzedPdfResultsItem.categoryIncluded = analyzedPdfResults[i].category_list.toString();
            analyzedPdfResultsItem.terminologyType = analyzedPdfResults[i].analyzeType ? analyzedPdfResults[i].analyzeType.toString() : '';
            analyzedPdfResultsItem.analysedDate = analyzedPdfResults[i].analyzed_date;
            analyzedPdfResultsItem.highlight_pdf = analyzedPdfResults[i].highlight_pdf;
            analyzedPdfResultsItem.report_pdf = analyzedPdfResults[i].report_pdf;
            analyzedPdfResultsItem.analyzed_json = analyzedPdfResults[i].analyzed_json;
            await this.processAnalyzedResults(analyzedPdfResults[i]).then((results: any) => {
              analyzedPdfResultsItem.analysedByProfileImg = results.data.profileImageURL;
              analyzedPdfResultsLists.push(analyzedPdfResultsItem)
              analyzedPdfResultsLists.sort((a, b) => {
                if ((new Date(a.analysedDate)).getTime() > (new Date(b.analysedDate)).getTime()) { return -1; }
                if ((new Date(a.analysedDate)).getTime() < (new Date(b.analysedDate)).getTime()) { return 1; }
              });
            });
          }
        }
        this.analyzedList = analyzedPdfResultsLists;
      }, err => {
        this.spinner.hide();
      }
    );
  }

  async processAnalyzedResults(element) {
    return new Promise(async (resolve, reject) => {
      await this.dataService.getSingle(element.analyze_by.user_id, this.configuration.user + '/').subscribe(
        (data: any) => {
          resolve(data)
        })
    });
  }

  updateQuestionarie() {
    // if (this.questionarieForm.valid) {
    let questionarieForm = this.questionarieForm.value;

    let questionarieModel = {
      workshop: this.selectedWorkshopId,
      participant: questionarieForm.participant,
      file: this.fileDetails,
      createdBy: this.createdBy,
      updatedBy: this.storageConfiguration.sessionGetItem(this.storageConfiguration.id),
      status: this.questionarieStatus ? this.questionarieStatus : ''
    }
    this.dataService.updateUser(this.questionarieId, this.configuration.questionaire + '/', questionarieModel).subscribe(
      (data: any) => {

        this.toastr.success('Questionarie Updated Successfully.');
      }, err => {
        this.toastr.error(err.error.message);
      }
    );
  }

  fileUpload(event) {
    // debugger;
    if (event.target.files.length > 0) {
      const formData = new FormData();
      let fileDetails = event.target.files[0];
      formData.append('file', fileDetails);
      this.spinner.show();
      this.dataService.addFile(this.configuration.upload, formData).subscribe(
        (data: any) => {
          this.fileName = data.fileDetails.fileNameName;
          this.fileDetails = {
            fileName: data.fileDetails.fileNameName,
            originalFileName: data.fileDetails.fileOriginalName,
            fileExt: data.fileDetails.fileExt,
            url: data.fileDetails.url,
          }
          this.toastr.success('File Uploaded Successfully!!');
          this.spinner.hide();
        }, err => {
          this.spinner.hide();
        }
      );

    }
  }

  getWorkshopList() {
    this.dataService.getAll(this.configuration.workshop + '?sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
      let workshopList = data.data;
      this.workshopList = this.workshopListTableData(workshopList);
      this.getFilteredValue();
    }, err => {
    });
  }

  getFilteredValue() {
    if (this.workshopList.length > 0) {
      this.filteredOptions = this.myControl.valueChanges.pipe(
        startWith(''),
        map(data => this._filter(data))
      );
    }
  }

  private _filter(value: string): any[] {
    let workshopData = JSON.stringify(this.workshopList);
    let workshopList = JSON.parse(workshopData);
    const filterValue = value.toLowerCase();
    return workshopList.filter(option => option.name
      .toString()
      .toLowerCase()
      .includes(filterValue)
    );
  }

  onSelectedUser(event) {
    let data = event.option.id;
    this.selectedWorkshopId = data.id;

    this.questionarieForm.patchValue({
      workshopDate: data.workshopDate,
      workshopConductedBy: data.conductedBy.firstName + ' ' + data.conductedBy.lastName
    });
    this.getParticipantList();

  }

  selectedParticipant(event) {
    this.questionarieForm.patchValue({
      managerTitle: event.managerTitle,
      participantTitle: event.participantTitle,
      managerName: event.managerName,
      organizationName: event.organizationName,
      mobile: event.mobile,
      officeMobile: event.officeMobile
    });
  }

  async searchWorkshop() {
    if (this.myControl.value != undefined && this.myControl.value != "") {
      this.workshopList = [];
      await this.dataService.getAll(this.configuration.workshop + '?name=' + this.myControl.value).subscribe((data: any) => {
        let workshopList = data.data;
        this.workshopList = this.workshopListTableData(workshopList);
        this.getFilteredValue();
      }, err => {
      });
    }
    else {
      this.getWorkshopList();
    }
  }

  workshopListTableData(userData) {
    let userListData = [];
    userData.forEach((element, index) => {
      userListData.push({
        id: element._id,
        name: element.name,
        workshopDate: element.workshopDate,
        conductedBy: element.conductedBy,
      });
    });

    return userListData;
  }


  getParticipantList() {
    this.participantList = [];
    this.dataService.getAll(this.configuration.participant + '?workshop=' + this.selectedWorkshopId).subscribe((data: any) => {
      let participantList = data.data;
      this.participantList = this.participantListTableData(participantList);
    }, err => {
    });
  }

  participantListTableData(workData) {
    let participantListData = [];
    workData.forEach((element, index) => {
      participantListData.push({
        id: element._id,
        fullName: element.firstName + ' ' + element.lastName,
        firstName: element.firstName,
        lastName: element.lastName,
        participantTitle: element.title,
        organizationName: element.organizationName,
        mobile: element.address.mobile,
        officeMobile: element.address.officeMobile,
        managerTitle: element.manager.name,
        managerName: element.manager.title,
      });
    });
    return participantListData;
  }

  analysePDF() {
    if (this.fileName !== '') {
      let message = {
        message: 'Do you want to analyze pdf?'
      }
      this.spinner.show();
      let categories = []
      this.dataService.getAll(this.configuration.category + '?sortBy=sortOrder&orderBy=asc').subscribe((data: any) => {
        let categoryList = data.data;
        if (categoryList.length > 0) {
          categoryList.forEach((element, index) => {
            let name = element.type;
            let checkBoxItems = {
              name: name,
              checked: true
            }
            categories.push(checkBoxItems)
          })
        } else {
          return this.toastr.error('Please create categories before analyzing!')
        }
        this.spinner.hide();
      }, err => {
        this.spinner.hide();
      });
      let customAnalyzeDialogRef = this.dialog.open(CustomAnalyzeComponent, {
        panelClass: 'confirm',
        disableClose: false,
        width: '700px',
        data: categories
      });
      customAnalyzeDialogRef.afterClosed().subscribe(result => {
        if (result.response) {
          let userId = this.storageConfiguration.sessionGetItem(this.storageConfiguration.id)
          let requestURL = this.questionarieId + '/' + userId + '?analyzeType=' + result.analyzeType;
          let selectedCategories = result.data;
          let isAllSelected = selectedCategories.filter(x => x.checked == false)
          if (isAllSelected.length > 0) {
            let categoryIncludedList = selectedCategories.reduce((a, o) => (o.checked && a.push(o.name), a), []);
            let categoriesSelected = categoryIncludedList.join();
            requestURL = this.questionarieId + '/' + userId + '?type=' + categoriesSelected + '&analyzeType=' + result.analyzeType;
          }
          this.confirmDialogRef = this.dialog.open(ConfirmDialogComponent, {
            panelClass: 'confirm',
            disableClose: false,
            data: message
          });
          this.confirmDialogRef.afterClosed().subscribe(result => {
            if (result) {
              this.spinner.show();
              this.dataService.getSingleUser(requestURL, this.configuration.analyze).subscribe(
                (data: any) => {
                  this.tabIndex = 1;
                  this.spinner.hide();
                  this.toastr.success('Analyzed PDF Successfully!!');
                  this.editQuestionarieDetails();
                  this.questionarieStatus = 'analyzed';
                  this.updateQuestionarie();
                }, err => {
                  this.toastr.error('PDF analyze Failed!!');
                  this.spinner.hide();
                });
            }
          });
        }
      })
    }
    else {
      this.toastr.error('Please upload pdf');
    }
  }

  reportPdf(report_pdf) {
    if (report_pdf != '') {
      window.open(report_pdf);
    }
    else {
      this.toastr.warning('Please analyze pdf');
    }
  }

  viewPdf() {
    if (this.pdfUrl != '') {
      window.open(this.pdfUrl);
    }
    else {
      this.toastr.warning('There is no pdf. Please upload');
    }
  }

  downloadPdf(pdfUrl) {
    if (pdfUrl != '') {
      window.open(pdfUrl);
    }
    else {
      this.toastr.warning('There was an error loading the file');
    }
  }

  deleteQuestionarie() {
    let message = {
      message: 'Do you want to delete this questionarie?'
    }
    this.confirmDialogRef = this.dialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      disableClose: false,
      data: message
    });
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dataService.delete(this.questionarieId, this.configuration.questionaire + '/').subscribe(
          (data: any) => {
            this.toastr.success('Questionarie deleted successfully!!');
            this.router.navigate(['/map/questionarie']);
          }, err => {
          }
        );
      }
    });
  }


}
