import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { QuestionarieListComponent } from './questionarie-list.component';
import { QuestionarieDetailsComponent } from './questionarie-details.component';

const routes: Routes = [
  { path: '', component: QuestionarieListComponent },
  { path: 'questionarie-details', component: QuestionarieDetailsComponent },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class QuestionarieRoutingModule { }
