import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { UntypedFormGroup, UntypedFormBuilder, Validators, UntypedFormControl } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { ConfirmDialogComponent } from 'src/app/shared/confirm-dialog/confirm-dialog.component';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';

@Component({
  selector: 'app-questionarie-list',
  templateUrl: './questionarie-list.component.html',
  styleUrls: ['./questionarie-list.component.scss']
})
export class QuestionarieListComponent implements OnInit {

  displayedColumns: string[] = ['position', 'workshopName', 'participantName', 'workshopDate', 'createdBy', 'createdDate', 'status', 'actions'];
  dataSource = new MatTableDataSource(ELEMENT_DATA);
  userForm: UntypedFormGroup;
  selectparticipationpopup: any;
  uploadquestionariespdfpopup: any;
  fileDetails: any;
  totalRecords: any;
  questionarieList = [];
  pageNumber = 1;
  confirmDialogRef: any;
  @ViewChild(MatSort, { static: true }) sorting: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  workshopSearch = new UntypedFormControl();
  participantSearch = new UntypedFormControl();
  workshopfilteredOptions: Observable<any[]>;
  participantfilteredOptions: Observable<any[]>;
  workshopList = [];
  selectedWorkshopId: any;
  participantList = [];
  selectedParticpantId: any;
  statusFilter = '';
  roleName: any;
  id: any;
  path: string;

  constructor(
    private fb: UntypedFormBuilder,
    public dialog: MatDialog,
    private router: Router,
    private configuration: Configuration,
    private dataService: DataService,
    private storageConfiguration: StorageConfiguration,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private _matDialog: MatDialog,
    private route: ActivatedRoute,

  ) {
    this.userForm = this.fb.group({
      CategoryName: ['', Validators.required],
      numberofsynonyms: ['', Validators.required],
      createddate: ['', Validators.required],
      createdby: ['', Validators.required],
      colorpicker: ['', Validators.required]
    })
  }

  ngOnInit() {
    this.roleName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.role)
    this.id = this.storageConfiguration.sessionGetItem(this.storageConfiguration.id)
    this.route.params.subscribe(params => {
      this.statusFilter = params.status;
      this.workshopSearch.setValue('');
      if (this.roleName == 'staff') {
        this.path = 'sortBy=createdDate&orderBy=desc&createdBy=' + this.id
      }
      else {
        this.path = 'sortBy=createdDate&orderBy=desc'
      }
    });
    this.getQuestionarieList();
  }

  getQuestionarieList() {
    this.spinner.show();
    this.questionarieList = [];
    let queryParams = '?' + this.path
    if (this.statusFilter != '' && this.statusFilter != undefined) {
      queryParams = queryParams + '&status=' + this.statusFilter
    }
    this.dataService.getAll(this.configuration.questionaire + queryParams).subscribe((data: any) => {
      let questionarieList = data.data;
      this.totalRecords = data.totalRecords;
      this.questionarieList = this.questionarieListTableData(questionarieList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  questionarieListTableData(questionarieList) {
    let questionarieListData = [];
    questionarieList.forEach((element, index) => {
      questionarieListData.push({
        questionarieId: element._id,
        position: (index + 1) + (this.pageNumber - 1) * 10,
        workshopName: element.workshop.name,
        workshopDate: element.workshop.workshopDate,
        createdDate: element.createdDate,
        status: element.status ? element.status : '',
        createdBy: element.createdBy ? element.createdBy.firstName + ' ' + element.createdBy.lastName : '',
        createdByImage: element.createdBy ? element.createdBy.profileImageURL : '',
        organizationName: element.participant ? element.participant.organizationName : '',
        participantName: element.participant ? element.participant.firstName + ' ' + element.participant.lastName : '',
      });
    });
    return questionarieListData;
  }

  nextPage(event) {
    this.spinner.show();
    this.pageNumber = event.pageIndex + 1;
    let queryParams = this.configuration.questionaire + '?pageNumber=' + this.pageNumber + '&' + this.path;
    if (this.statusFilter != '' && this.statusFilter != undefined) {
      queryParams = queryParams + '&status=' + this.statusFilter;
    }
    this.dataService.getAll(queryParams).subscribe((data: any) => {
      let questionarieList = data.data;
      this.totalRecords = data.totalRecords;
      this.questionarieList = this.questionarieListTableData(questionarieList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }


  getWorkshopFilteredValue() {
    if (this.workshopList.length > 0) {
      this.workshopfilteredOptions = this.workshopSearch.valueChanges.pipe(
        startWith(''),
        map(data => this._filter(data))
      );
    }
  }
  getParticipantFilteredValue() {
    if (this.participantList.length > 0) {
      this.participantfilteredOptions = this.participantSearch.valueChanges.pipe(
        startWith(''),
        map(data => this._filter(data))
      );
    }
  }

  private _filter(value: string): any[] {
    let workshopData = JSON.stringify(this.workshopList);
    let workshopList = JSON.parse(workshopData);
    const filterValue = value.toLowerCase();
    return workshopList.filter(option => option.name
      .toString()
      .toLowerCase()
      .includes(filterValue)
    );
  }

  onSelectedWorkshop(event) {
    let data = event.option.id;
    this.selectedWorkshopId = data.id;

    this.searchQuestionnaire();

  }

  async searchQuestionnaire() {
    if (this.selectedWorkshopId != undefined && this.selectedWorkshopId != "") {
      let queryParams = this.configuration.questionaire + '?workshop=' + this.selectedWorkshopId + '&' + this.path;
      if (this.statusFilter != '' && this.statusFilter != undefined) {
        queryParams = queryParams + '&status=' + this.statusFilter
      }
      await this.dataService.getAll(queryParams).subscribe((data: any) => {
        let questionnaireList = data.data;
        this.totalRecords = data.data.length;
        this.pageNumber = 1;
        this.paginator.pageIndex = 0;
        this.questionarieList = this.questionarieListTableData(questionnaireList);
      }, err => {
        this.spinner.hide();
      });
    }
  }

  async searchWorkshop() {
    if (this.workshopSearch.value != undefined && this.workshopSearch.value != "") {
      this.workshopList = [];
      await this.dataService.getAll(this.configuration.workshop + '?name=' + this.workshopSearch.value).subscribe((data: any) => {
        let workshopList = data.data;
        this.workshopList = this.workshopListTableData(workshopList);
        this.getWorkshopFilteredValue();
      }, err => {
      });
    }
    else {
      this.getWorkshopList();
      this.getQuestionarieList();
    }
  }

  workshopListTableData(workshopSearchData) {
    let workshopListData = [];
    workshopSearchData.forEach((element, index) => {
      workshopListData.push({
        id: element._id,
        name: element.name,
        workshopDate: element.workshopDate,
        conductedBy: element.conductedBy,
      });
    });

    return workshopListData;
  }
  participantListTableData(participantSearchData) {
    let participantListData = [];
    participantSearchData.forEach((element, index) => {
      participantListData.push({
        id: element._id,
        name: element.firstName + ' ' + element.lastName,
      });
    });

    return participantListData;
  }

  getWorkshopList() {
    this.dataService.getAll(this.configuration.workshop + '?' + this.path).subscribe((data: any) => {
      let workshopList = data.data;
      this.workshopList = this.workshopListTableData(workshopList);
      this.getWorkshopFilteredValue();
    }, err => {
    });
  }
  getParticipantList() {
    this.dataService.getAll(this.configuration.participant + '?' + this.path).subscribe((data: any) => {
      let participantList = data.data;
      this.participantList = this.participantListTableData(participantList);
      this.getWorkshopFilteredValue();
    }, err => {
    });
  }

  deleteQuestionarie(questionarieId) {
    let message = {
      message: 'Do you want to delete this questionarie?'
    }
    this.confirmDialogRef = this._matDialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      disableClose: false,
      data: message
    });
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dataService.delete(questionarieId, this.configuration.questionaire + '/').subscribe(
          (data: any) => {
            this.toastr.success('Questionarie deleted successfully!!');
            this.getQuestionarieList();
          }, err => {
          }
        );
      }
    });
  }

  addquestionaries() {
    this.router.navigate(['/map/questionarie/questionarie-details']);
  }

  viewQuestionaries(questionarieId) {
    this.router.navigate(['/map/questionarie/questionarie-details', { questionarieId: questionarieId }]);
  }


}
export interface PeriodicElement {
  position: number;
  categoryname: string;
  synonyms: number;
  createdby: string;
  report: string;
  createddate: string;
}
const ELEMENT_DATA: PeriodicElement[] = [

];
